	}

	.defaultBtn {
		background: #d5d5d5;
	}

	button::after {
		border: none;
	}
	.top-warp {
		z-index: 9990;
		position: fixed;
		top: --window-top;
		/* css变量 */
		left: 0;
		width: 100%;
		background-color: white;
	}
	.scrollview {
		overflow-x: auto;
		-webkit-overflow-scrolling: touch;
		-webkit-mask: -webkit-gradient(linear, left top, right bottom, from(rgba(0, 0, 0, 1)), color-stop(0.75, #fff), to(rgba(0, 0, 0, 0)));
	}
</style>
